import styled from "styled-components";

export const Button = styled.button`
  width: 100px;
  height: 40px;
  background: none;
  backdrop-filter: blur(20px) saturate(2);
  border-radius: 15px;
  font-weight: 700;
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.6);
  border: none;
  cursor: pointer;
  transition: transform 0.3s ease;

  &:active {
    transform: scale(1.2);
  }
`;
