const axios = require("axios");

const axs = axios.create({
  baseURL: "https://api.prime.date",
  mode: "cors",
  withCredentials: true,
  referrer: "https://prime.date/",
  "X-Remote-IP": "127.0.0.1",
  referrerPolicy: "no-referrer-when-downgrade",
  headers: { accept: "application/json", "content-type": "application/json" }
});

const models = [
  36516324,
  8304691,
  24789739,
  31596719,
  5660047,
  36067275,
  36064530,
  36063606,
  38153955,
  38154561,
  15403705,
  36667225,
  39368872,
  28262217,
  14217338,
  4559434,
  39166856,
  25541235,
  2765367,
  17415843,
  4411030
];

export const getStatistics = (cb, all) => {
  const today = new Date();
  const tomorrow = new Date();
  tomorrow.setDate(today.getDate() + 1);

  let todayDate = {
    year: today.getFullYear(),
    month: today.getMonth() + 1,
    day: today.getHours() < 3 ? today.getDate() - 1 : today.getDate()
  };

  let tomorrowDate = {
    year: tomorrow.getFullYear(),
    month: tomorrow.getMonth() + 1,
    day: tomorrow.getDate()
  };

  const fetchStats = id =>
    axs({
      url: `/statistic/agency?dateFrom=${2019 /*todayDate.year*/}-${
        10 /*todayDate.month*/
      }-${31 /*todayDate.day*/}&dateTo=${2019 /*tomorrowDate.year*/}-${
        11 /*tomorrowDate.month*/
      }-${1 /*tomorrowDate.day*/}&groupByDate=0&idLady=${id}`,
      method: "GET"
    });

  Promise.all(models.map(model => fetchStats(model))).then(r =>
    cb(
      r.map(e => {
        return all === true
          ? e.data.data.map(x => x)
          : e.data.data.reduce((a, b) => ({
              lady: a.lady,
              bonuses: parseFloat(a.bonuses) + parseFloat(b.bonuses),
              operators: [a.operator, b.operator]
            }));
      })
    )
  );
};

export const getModels = cb => {
  const parseModel = id =>
    axs({
      url: `https://api.prime.date/operator/get-female/${id}`
    });

  Promise.all(models.map(model => parseModel(model))).then(data =>
    cb(data.map(x => x.data.data))
  );
};
