import React, { Component } from "react";
import { getStatistics, getModel, getModels } from "./api";
import { UseGlobalStyle } from "./ui/pages/UseGlobalStyle";
import { Background, ColorTag, Button } from "./ui/atoms";
import { Modal } from "./ui/organisms/Modal";
import { StatsTable } from "./ui/molecules/StatsTable";

class PdAdmin extends Component {
  constructor(props) {
    super(props);

    this.state = {
      total: 0,
      balances: [],
      statsModeAll: true
    };

    this.changeStatsMode = this.changeStatsMode.bind(this);
  }

  getTotal(data) {
    const total = data
      .map(x => x)
      .flat()
      .map(operator => parseFloat(operator.bonuses));
    return this.setState({ total: total.reduce((a, b) => a + b) });
  }

  setBalanceColor(bonuses) {
    // const red = (bonuses < 30) && 'red'
    // const orange =  (bonuses > 30 && bonuses < 50) && 'orange'
    // const green = (bonuses > 50 && bonuses < 100) && 'green'
    switch (true) {
      case bonuses < 30:
        return "rgb(255,59,48)";
      case bonuses > 30 && bonuses < 50:
        return "rgb(255,149,0)";
      case bonuses > 50 && bonuses < 100:
        return "rgb(52,199,89)";
      case bonuses > 100 && bonuses < 150:
        return "rgb(88,86,214)";
      case bonuses > 150 && bonuses < 200:
        return "rgb(175,82,222)";

      default:
        return "white";
    }
  }

  showOperatorsBalance() {
    return this.state.balances.map(operator => (
      <React.Fragment>
        <tr>
          <td>{operator.lady}</td>
          <td>
            <ColorTag color={this.setBalanceColor(operator.bonuses)}>
              {parseFloat(operator.bonuses).toFixed(2) + "$"}
            </ColorTag>
          </td>
          <td>{Math.round(operator.bonuses / 0.055)}</td>
          <td>
            {!!operator.operators && !this.state.statsModeAll ? (
              operator.operators.map(el => <span>{el}</span>)
            ) : (
              <span>{operator.operator}</span>
            )}
          </td>
        </tr>
      </React.Fragment>
    ));
  }

  changeStatsMode() {
    this.setState({ statsModeAll: !this.state.statsModeAll });
  }

  componentDidMount() {
    setInterval(
      () =>
        getStatistics(data => {
          this.setState({ balances: data.flat() });
          this.getTotal(data);
        }, this.state.statsModeAll),
      1000
    );
  }

  render() {
    console.log("this.state", this.state);
    document.title = `Team Balance | ${this.state.total.toFixed(2) - 61}$`;
    return (
      <React.Fragment>
        <UseGlobalStyle />
        <Background />

        <Modal w="1000px" h="750px">
          <StatsTable>
            <thead>
              <th>Model</th>
              <th>Balance ($)</th>
              <th>Bonuses</th>
              <th>Translators</th>
            </thead>
            <tbody>{this.showOperatorsBalance()}</tbody>
          </StatsTable>
        </Modal>
        <Modal w="150px" h="40px" total>
          <i className="fas fa-dollar-sign" />
          {this.state.total.toFixed(2) - 61}$
        </Modal>
        <Button onClick={this.changeStatsMode}>Mode</Button>
      </React.Fragment>
    );
  }
}

export default PdAdmin;
