import styled from "styled-components";

export const StatsTable = styled.table`
  border-spacing: 0px;
  line-height: 25px;
  cursor: default;

  thead {
    height: 30px;
    font-weight: 700;
  }

  th {
    background: rgba(0, 0, 0, 0.2);
    padding: 5px;
  }

  td {
    width: 250px;
    text-align: center;
  }

  td span {
    margin: 0 10px;
  }

  tr:nth-child(even) {
    background: rgba(0, 0, 0, 0.2);
  }

  tr:hover {
    background: rgba(255, 255, 255, 0.3);
  }
`;
