import { createGlobalStyle } from "styled-components";

export const UseGlobalStyle = createGlobalStyle`
  html {
    font-size: 16px;
  }

  body {
    overflow: hidden;
    background: black;
    scrollbar-width: none;
  }

  #root {
    width: 100vw;
    height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'SF Display', Arial, Helvetica, sans-serif;
  }

`;
